@media (max-width:768px) {

    /*
    Mobile View/Tablet View Hidden
    */
    #Global_HomePageNavbarSection,
    #Mobile_HomePageNavbarSection {
        display: none;
    }

    #Tablet_HomePageNavbarSection {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        background-color: var(--base2-color);
        box-shadow: var(--box-shadow);
        height: 80px;
    }

    .Tablet_HomePageNavbarSection1 {
        align-items: center;
        padding-left: 20px;
    }

    .Tablet_HomePageLink {
        text-decoration: none;
    }

    #Tablet_HomePageCompanyTitle {
        align-items: center;
        text-shadow: var(--box-shadow);
        font-size: 1.5rem;
        font-weight: bold;
        color: red;
    }

    .Tablet_HomePageNavbarSection2 {
        padding-right: 20px;
        display: flex;
        box-sizing: border-box;
    }

    .Tablet_HomePageNavbarSection2ListItem {
        padding-left: 5px;
        padding-right: 5px;
        list-style-type: none;
    }

    .Tablet_HomePageNavLink {
        font-size: 1rem;
        text-decoration: none;
    }

    .Tablet_HomePageNavLinkTitle {
        font-weight: bold;
        color: red;
    }

    .Tablet_getStartedItem {
        list-style-type: none;
        padding-left: 10px;
    }

    .Tablet_HomePageGetStarted {
        box-shadow: var(--box-shadow);
        background-color: red;
        border-radius: 8px;
    }

    .Tablet_HomePageGetStartedNavLink {
        color: white;
    }


}