/*CUSTOM COLORS*/
:root {
    /*--Colors--*/
    --base-color: #161618;
    --base2-color: #1C1C1E;
    --trim-color: #000000;
    --primary-color: #212124;
    --secondary-color: #818181;
    --test-color: rgb(2, 211, 248);
    --error-color: ;
    --pass-color: ;
    --clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
    /*--Sizes--*/
    --full-height: 100vh;
    --full-width: 100vw;
    --box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

@media (max-width:800px) {

    /* Mobile View/Tablet View Hidden*/
    #Global_HomePageMainSection,
    #Mobile_HomePageMainSection {
        display: none;
    }

    #Tablet_HomePageMainSection {
        display: block;
        color: #818181;
        box-shadow: var(--box-shadow);
        align-items: center;
    }


    .Tablet_Introduction {
        padding-inline: 10px 10px;
        height: 90%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: 'center';
        box-shadow: var(--box-shadow);
        width: min(350px);
        margin: auto;
    }

    .Tablet_HomePageIntro {
        justify-content: center;
        margin: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        border-bottom: 1px solid;
        width: min(350px);
    }

    .Tablet_HomePageIntroButtonSection {
        padding: 10px;
        width: 350px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        width: min(350px);
    }

    .Tablet_HomePageIntroButtons {
        border-radius: 8px;
        border-style: groove;
        height: 30px;
        width: 150px;
        height: 40px;
        background-color: red;
    }

    .Tablet_HomePageIntroButtonLink {
        text-decoration: none;
        font-weight: bold;
        font-size: 18px;
        color: white;
    }
}