/*CUSTOM COLORS*/
:root {
    /*--Colors--*/
    --base-color: #161618;
    --base2-color: #1C1C1E;
    --trim-color: #000000;
    --primary-color: #212124;
    --secondary-color: #818181;
    --test-color: rgb(2, 211, 248);
    --error-color: ;
    --pass-color: ;
    --clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
    /*--Sizes--*/
    --full-height: 100vh;
    --full-width: 100vw;
    --box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.Footer {
    display: flex;
    flex-direction: column;
    color: white;
}

.FooterBox {
    display: flex;
    flex-direction: row;
}

.FlexTicks {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

iframe.github {
    border: 0px;
    border-radius: 8px;
    height: 30px;
    width: 116px;
}


.FooterCategory {
    font-weight:bold;
    width: 200px;
    height: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: .1px solid black;
    border-radius: 20px;
    box-shadow: var(--box-shadow);

}

.FooterCategory hr {
    width: 99%;
}

.FooterCategoryList {
    padding: 20px;
    list-style-type: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.FooterCategoryItem {
    padding: 2px;
    text-align: center;

}

.FooterCategoryItemLink {
    color: red;
    text-decoration: none;
}

.SubList {
    visibility: hidden;
}