/*CUSTOM COLORS*/
:root {
    /*--Colors--*/
    --base-color: #161618;
    --base2-color: #1C1C1E;
    --trim-color: #000000;
    --primary-color: #212124;
    --secondary-color: #818181;
    --test-color: rgb(2, 211, 248);
    --error-color: ;
    --pass-color: ;
    --clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
    /*--Sizes--*/
    --full-height: 100vh;
    --full-width: 100vw;
    --box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

@media (max-width:390px) {

    /* Mobile View/Tablet View Hidden*/
    #Global_HomePageMainSection,
    #Tablet_HomePageMainSection {
        display: none;
    }

    #Mobile_HomePageMainSection {
        display: flex;
    }
}