/*CUSTOM COLORS*/
:root {
    /*--Colors--*/
    --base-color: #161618;
    --base2-color: #1C1C1E;
    --trim-color: #000000;
    --primary-color: #212124;
    --secondary-color: #818181;
    --test-color: rgb(2, 211, 248);
    --error-color: ;
    --pass-color: ;
    --clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
    /*--Sizes--*/
    --full-height: 100vh;
    --full-width: 100vw;
    --box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

#NavbarSection {
    padding-right: 5px;
}

.navbarList {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

strong.navLink a {
    color: black;
    text-decoration: dotted;
}

strong.navLink a:visited {
    color: black;
    text-decoration: dotted;
}

.navbarSection2 {
    color: black;
}

.navbarSection2List {
    list-style-type: none;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
}

.navbarSection2ListItem {
    padding-left: 20px;
    padding-right: 20px;
}

.navLink {
    color: red;
    text-decoration: none;
}

/*Navbar Buttons*/
.navbarItem {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 10px;
    font-size: 20px;
}

.GetStarted {
    border-style: groove;
    height: 40px;
    background-color: red;
    border-radius: 8px;
    color: white;
    font-size: 18px;
    font-weight: bold;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: var(--box-shadow);
}