/*CUSTOM COLORS*/
:root {
    /*--Colors--*/
    --base-color: #161618;
    --base2-color: #1C1C1E;
    --trim-color: #000000;
    --primary-color: #212124;
    --secondary-color: #818181;
    --test-color: rgb(2, 211, 248);
    --error-color: ;
    --pass-color: ;
    --clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
    /*--Sizes--*/
    --full-height: 100vh;
    --full-width: 100vw;
    --box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

/* Mobile View/Tablet View Hidden*/
#Tablet_HomePageMainSection,
#Mobile_HomePageMainSection {
    display: none;
}

#Global_HomePageMainSection {
    box-shadow: var(--box-shadow);
    display: flex;
    padding-top:50px ;
    padding-bottom:50px ;
}

#Global_HomePageMainSection {
    color: #818181;
}


.Global_Introduction {
    padding-inline: 10% 10%;
    height: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: 'center';
    box-shadow: var(--box-shadow);
    margin-left: min(125px);
    margin-right: min(125px);
}

.Global_HomePageIntro {
    justify-content: center;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-bottom: 1px solid;
}

.Global_HomePageIntroButtonSection {
    padding: 10px;
    width: 350px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.Global_HomePageIntroButtons {
    border-radius: 8px;
    border-style: groove;
    height: 30px;
    width: 150px;
    height: 40px;
    background-color: red;
}

.Global_HomePageIntroButtonLink {
    text-decoration: none;
    font-weight: bold;
    font-size: 18px;
    color: white;
}