/*CUSTOM COLORS*/
:root {
    /*--Colors--*/
    --base-color: #161618;
    --base2-color: #1C1C1E;
    --trim-color: #000000;
    --primary-color: #212124;
    --secondary-color: #818181;
    --test-color: rgb(2, 211, 248);
    --error-color: ;
    --pass-color: ;
    --clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
    /*--Sizes--*/
    --full-height: 100vh;
    --full-width: 100vw;
    --box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

@media (max-width:800px) {

    #Global_HomePageFooterSection,
    #Mobile_HomePageFooterSection {
        display: none;
    }

    #Tablet_HomePageFooterSection {
        display: flex;
        flex-direction: column;
        color: yellow;
        box-shadow: var(--box-shadow);
    }
    
    .Tablet_HomePageFooterCards {
        margin: 0;
        padding: 0;
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        justify-content: space-evenly;
    }
    
    .Tablet_HomePageFooterCategory {
        display: flex;
        flex-direction: column;
    
        width: 250px;
        align-items: center;
        border: 1px solid black;
        box-shadow: var(--box-shadow); 
        border-radius: 20px;
        height: 200px;
        padding: 0;
        margin:10px;
    
    }
    
    .Tablet_HomePageFooterCategoryName {
        margin: 0;
        padding: 0;
        text-decoration: none;
        padding: 0;
        color: purple;
        font-weight: bold;
        font-size: 1.2rem;
    }
    
    .Tablet_HomePageFooterCategoryNameBar {
        margin: 0;
        padding: 0;
        width: 100%;
    }
    
    .Tablet_HomePageFooterCategoryList {
        margin-top: 20px;
        padding: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        list-style-type: none;
        padding: 0;
        padding-bottom: 10px;
    }
    
    .Tablet_HomePageFooterCategoryItem {
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        text-align: center;
        padding-bottom: 5px;
    }
    
    .Tablet_HomePageFooterCategoryItemLink {
        margin: 0;
        padding: 0;
        text-decoration: none;
        color: red;
        font-weight: bold;
        font-size: 1rem;
    }
    
    .Tablet_HomePageSubList {
        margin: 0;
        padding: 0;
        visibility: hidden;
    }
    
    .Tablet_github {
        margin: 0;
        padding: 0;
        width: 125px;
    
        border: 0;
        height: 32px;
        border-radius: 8px;
    }
    
    .Tablet_HomePageFooterBox {
        margin: 0;
        padding: 0;
        display: block;
    }
}